:root {
    --bg-main: #1a1a1a;
    --bg-card: #2d2d2d;
    --text-primary: #ffffff;
    --text-secondary: #b3b3b3;
    --border-color: #404040;
    --hover-color: #3a3a3a;
    --delete-color: #ff4444;
    --checkbox-color: #4CAF50;
    --button-bg: #4CAF50;
  }
  
  body {
    background-color: var(--bg-main);
    color: var(--text-primary);
  }
  
  .todo-container {
    max-width: 500px;
    margin: 2rem auto;
    padding: 2rem;
    background: var(--bg-card);
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  }
  
  h1 {
    text-align: center;
    margin-bottom: 1.5rem;
    color: var(--text-primary);
  }
  
  .add-task-form {
    display: flex;
    gap: 8px;
    margin-bottom: 1.5rem;
  }
  
  .task-input {
    flex: 1;
    padding: 8px 12px;
    border: 1px solid var(--border-color);
    border-radius: 4px;
    font-size: 1rem;
    background-color: var(--bg-main);
    color: var(--text-primary);
  }
  
  .task-input::placeholder {
    color: var(--text-secondary);
  }
  
  .task-input:focus {
    outline: none;
    border-color: var(--button-bg);
    box-shadow: 0 0 0 2px rgba(76, 175, 80, 0.2);
  }
  
  .add-button {
    padding: 8px 16px;
    background: var(--button-bg);
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1.2rem;
    transition: background-color 0.2s;
  }
  
  .add-button:hover {
    background: #45a049;
  }
  
  .tasks-list {
    margin-bottom: 1.5rem;
  }
  
  .task-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px 0;
    border-bottom: 1px solid var(--border-color);
  }
  
  .task-label {
    display: flex;
    align-items: center;
    gap: 8px;
    flex: 1;
  }
  
  .task-label input[type="checkbox"] {
    width: 18px;
    height: 18px;
    accent-color: var(--checkbox-color);
    cursor: pointer;
  }
  
  .completed {
    text-decoration: line-through;
    color: var(--text-secondary);
  }
  
  .delete-button {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 1.2rem;
    opacity: 0.6;
    padding: 4px;
    color: var(--text-secondary);
    transition: all 0.2s;
  }
  
  .delete-button:hover {
    opacity: 1;
    color: var(--delete-color);
  }
  
  .todo-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: var(--text-secondary);
    font-size: 0.9rem;
    margin-top: 1rem;
    padding-top: 1rem;
    border-top: 1px solid var(--border-color);
  }
  
  .clear-button {
    background: none;
    border: none;
    color: var(--text-secondary);
    cursor: pointer;
    padding: 4px 8px;
    border-radius: 4px;
    transition: all 0.2s;
  }
  
  .clear-button:hover {
    color: var(--text-primary);
    background: var(--hover-color);
  }